* {
  box-sizing: border-box;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote::before,
blockquote::after,
q::before,
q::after {
  content: "";
  content: none;
}

:focus {
  outline: 0;
}

ins {
  text-decoration: none;
}

del {
  text-decoration: line-through;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  border: none;
  outline: none;
  box-shadow: none;
  padding: 0;
  background-color: transparent;
}

img {
  border-style: none;
}

input:disabled {
  opacity: 1;
}

html,
body {
  width: 100%;
  height: 100%;
}

.container {
  max-width: 1200px;
  padding: 20px 0;
  min-height: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.header,
.navigation,
.content,
.aside-menu {
  background: #eee;
  margin-bottom: 20px;
}

.wrapper {
  display: flex;
  flex-direction: row;
}

.logo {
  position: relative;
  margin-right: 10px;
  width: 350px;
  height: 150px;
  flex-shrink: 0;
}

.logo-image {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.header {
  padding: 20px;
}

.head {
  margin-left: 10px;
}

.main {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.wrapper-main {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
}

.navigation {
  padding: 20px;
  flex-basis: 20%;
  margin-right: 20px;
}

.navigation-item1 {
  margin-bottom: 20px;
}

.content {
  padding: 20px;
  flex-basis: 60%;
}

.footer article {
  word-break: break-all;
}

.content article:not(:last-child) {
  margin-bottom: 40px;
}

.aside-menu {
  padding: 20px;
  flex-basis: 20%;
  margin-left: 20px;
}

.footer {
  display: flex;
  padding: 20px;
  background: #eee;
  flex-direction: row;
  justify-content: space-between;
}

.footer article:not(:last-child) {
  margin-right: 20px;
}

/* together */
.navigation li:not(:last-child),
.aside-menu li:not(:last-child) {
  margin-bottom: 10px;
}

.content article h2,
.content article p,
.footer article p:not(:last-child) {
  margin-bottom: 10px;
}

.main,
.header,
.footer {
  flex-shrink: 0;
}
